import Page from 'flarum/components/Page';
import IndexPage from 'flarum/components/IndexPage';
import listItems from 'flarum/common/helpers/listItems';
import LoadingIndicator from "flarum/components/LoadingIndicator";
import GuaGuaLeListItem from "./GuaGuaLeListItem";
import Button from 'flarum/components/Button';

export default class GuaGuaLeIndexPage extends Page {
  oninit(vnode) {
    super.oninit(vnode);
    this.bodyClass = 'App--index';
    this.loading = true;
    this.guagualeList = [];
    this.loadResults();
    //display: grid;grid-template-columns: repeat(2, 1fr);grid-template-rows: 150px;gap: 30px 10px;
  }

  view() {
    let loading;

    if (this.loading) {
      loading = LoadingIndicator.component({ size: "large" });
    }

    return (
      <div className="IndexPage">
        {IndexPage.prototype.hero()}

        <div className="container">
          <div className="sideNavContainer">
            <nav className="IndexPage-nav sideNav">
              <ul>{listItems(IndexPage.prototype.sidebarItems().toArray())}</ul>
            </nav>

            <div class="GuaGuaLeGlobalContainer">
              <div style="padding-bottom:10px;">
                {Button.component({
                    className: 'Button',
                    onclick: () => {
                      this.showPurchaseHistory();
                    }
                  },
                  app.translator.trans('wusong8899-guaguale.forum.guaguale-view-history')
                )}
              </div>
              {this.guagualeList.map((guagualeData) => {
                return (
                  <div style="padding-top:5px">
                    {GuaGuaLeListItem.component({ guagualeData })}
                  </div>
                );
              })}

              {!this.loading && this.guagualeList.length===0 && (
                <div>
                  <div style="font-size:1.4em;color: var(--muted-more-color);text-align: center;height: 300px;line-height: 100px;">{app.translator.trans("wusong8899-guaguale.forum.guaguale-list-empty")}</div>
                </div>
              )}

              {loading && <div className="GuaGuaLe-loadMore">{loading}</div>}
            </div>

          </div>
        </div>
      </div>
    );
  }

  parseResults(results) {
    [].push.apply(this.guagualeList, results);
    this.loading = false;
    m.redraw();
    return results;
  }

  loadResults() {
    return app.store
      .find("guagualeList")
      .catch(() => {})
      .then(this.parseResults.bind(this));
  }

  showPurchaseHistory() {
    m.route.set(app.route("user.guagualePurchaseHistory", {
      username: app.session.user.username(),
    }));
  }
}
