import Component from "flarum/Component";
import GuaGuaLeScratchModal from './GuaGuaLeScratchModal';

export default class GuaGuaLeHistoryListItem extends Component {
  view() {
    const {guagualeHistory} = this.attrs;
    const guagualePurchaseID = guagualeHistory.id();
    const guagualePurchaseTotalCost = guagualeHistory.pruchase_cost_total();
    const guagualePurchaseCount = guagualeHistory.pruchase_count();
    const guagualePurchaseCost = guagualeHistory.pruchase_cost();
    const guagualePurchaseDate = guagualeHistory.assignedAt();
    const guagualePurchaseOpened = guagualeHistory.opened();
    const guagualeData = guagualeHistory.guagualeData();
    const guagualeTitle = guagualeData.title();

    const moneyName = app.forum.attribute('antoinefr-money.moneyname') || '[money]';
    const purchaseCostTotalText = moneyName.replace('[money]', guagualePurchaseTotalCost);
    const guagualeResult = JSON.parse(guagualeHistory.pruchase_result());
    let guagualeWinPrice = 0;

    for(let index in guagualeResult){
      let winPrice = parseFloat(index);
      let winAmount = parseInt(guagualeResult[index]);

      if(winPrice!==0){
        guagualeWinPrice+=(winPrice*winAmount);
      }
    }

    const guagualeWinPriceText = moneyName.replace('[money]', this.precisionRound(guagualeWinPrice,2));

    return (
      <div className="guagualeHistoryContainer">
        <div style="padding-top: 5px;">
          <b>{app.translator.trans('wusong8899-guaguale.forum.guaguale-history-list-id')}: </b>
          {guagualePurchaseID}&nbsp;|&nbsp;
          <b>{app.translator.trans('wusong8899-guaguale.forum.guaguale-history-list-title')}: </b>
          {guagualeTitle}
        </div>
        <div style="padding-top: 5px;">
          <b>{app.translator.trans('wusong8899-guaguale.forum.guaguale-history-list-count')}: </b>
          {guagualePurchaseCount}{app.translator.trans("wusong8899-guaguale.forum.guaguale-unit-name")}&nbsp;|&nbsp;
          <b>{app.translator.trans('wusong8899-guaguale.forum.guaguale-history-list-cost')}: </b>
          {purchaseCostTotalText}&nbsp;|&nbsp;
          <b>{app.translator.trans('wusong8899-guaguale.forum.guaguale-history-list-assign-at')}: </b>
          {guagualePurchaseDate}&nbsp;|&nbsp;

          <b>{app.translator.trans('wusong8899-guaguale.forum.guaguale-history-list-open-result')}: </b>
          
          {guagualePurchaseOpened===1 && (
            <div style="display: inline-block;">
              <span style="padding-right:8px;">
                {guagualeWinPriceText}
              </span>
              <span class="GuaGuaLeButton--transparent" onclick={() => this.showResult(guagualeHistory,guagualePurchaseOpened)}>
                {app.translator.trans("wusong8899-guaguale.forum.guaguale-purchase-view-result")}
              </span>
            </div>
          )}
          {guagualePurchaseOpened===0 && (
            <div style="display: inline-block;">
              <span style="padding-right:8px;">
                {app.translator.trans("wusong8899-guaguale.forum.guaguale-purchase-not-scratch-yet")}
              </span>
              <span style="color:green" class="GuaGuaLeButton--transparent" onclick={() => this.showResult(guagualeHistory)}>
                {app.translator.trans("wusong8899-guaguale.forum.guaguale-purchase-scratch")}
              </span>
            </div>
          )}
        </div>
      </div>
    );
  }

  showResult(guagualeHistory) {
    app.modal.show(GuaGuaLeScratchModal, {guagualeHistory});
  }

  precisionRound(number, precision) {
    let factor = Math.pow(10, precision);
    return Math.round(number * factor) / factor;
  }
}
