import app from 'flarum/forum/app';
import { extend } from 'flarum/extend';
import addSidebarMenu from './addSidebarMenu';
import GuaGuaLeIndexPage from './components/GuaGuaLeIndexPage';
import addUserPage from "./addUserPage";

import GuaGuaLe from "./model/GuaGuaLe";
import GuaGuaLePurchase from "./model/GuaGuaLePurchase";
import GuaGuaLePurchaseSummary from "./model/GuaGuaLePurchaseSummary";
import GuaGuaLeNotification from "./components/GuaGuaLeNotification";
import NotificationGrid from "flarum/components/NotificationGrid";

app.initializers.add('wusong8899-raffle-ticket', () => {
  app.store.models.guagualeList = GuaGuaLe;
  app.store.models.guagualePurchase = GuaGuaLePurchase;
  app.store.models.guagualePurchaseHistorySummary = GuaGuaLePurchaseSummary;
  app.notificationComponents.guagualePurchase = GuaGuaLeNotification;

  app.routes['guaguale'] = {
    path: '/guaguale',
    component: GuaGuaLeIndexPage,
  };

  addSidebarMenu();
  addUserPage();

  extend(NotificationGrid.prototype, "notificationTypes", function (items) {
    items.add("guagualePurchase", {
      name: "guagualePurchase",
      icon: "fas fa-ticket-alt",
      label: app.translator.trans(
        "wusong8899-guaguale.forum.guaguale-receive-open-result"
      ),
    });
  });
});