import UserPage from "flarum/components/UserPage";
import GuaGuaLeHistoryList from "./GuaGuaLeHistoryList";

export default class GuaGuaLePurchaseHistoryPage extends UserPage {
  oninit(vnode) {
    super.oninit(vnode);
    this.loadUser(m.route.param("username"));
  }

  content() {
    return (
      <div className="GuaGuaLePurchaseHistoryPage">
        {GuaGuaLeHistoryList.component({
          params: {
            user: this.user,
          },
        })}
      </div>
    );
  }
}
