import Component from "flarum/Component";
import Link from "flarum/components/Link";
import Button from 'flarum/components/Button';
import GuaGuaLePurchaseModal from './GuaGuaLePurchaseModal';
import GuaGuaLeSoldOutModal from './GuaGuaLeSoldOutModal';
import LogInModal from "flarum/components/LogInModal";
import username from "flarum/helpers/username";

export default class GuaGuaLeListItem extends Component {
  oninit(vnode) {
    super.oninit(vnode);
    this.loading = true;
    this.guagualePurchaseList = [];
    this.loadResults();
  }

  view() {
    const {guagualeData} = this.attrs;
    const guagualeID = guagualeData.id();
    const guagualeTitle = guagualeData.title();
    const guagualeDesc = guagualeData.desc();
    const guagualeCost = guagualeData.cost();
    const guagualeImage = guagualeData.image();
    const guagualeColor = guagualeData.color();
    const guagualeAvailable = guagualeData.amount()-guagualeData.purchased();
    const guagualePurchaseData = guagualeData.guagualePurchaseData();
    const backgroundStyle = "background-image:url("+(guagualeImage===null?'':guagualeImage)+");background-color: "+(guagualeColor?guagualeColor:this.getBackgroundColor(guagualeTitle+"_"+guagualeID))+";";
    const maskStyle = guagualeImage===null?"":"background: var(--shadow-color);";
    const moneyName = app.forum.attribute('antoinefr-money.moneyname') || '[money]';
    const guagualePurchaseList = this.guagualePurchaseList;
    let guagualePurchasedUserText = "";

    for(let i=0;i<guagualePurchaseList.length;i++){
      const purchaseUser = guagualePurchaseList[i].purchasedUser();
      const purchaseUserName = purchaseUser.attribute("username");
      const purchaseCount = guagualePurchaseList[i].pruchase_count();
      const purchaseWinTotal = guagualePurchaseList[i].pruchase_win_total();
      const purchaseWinTotalText = moneyName.replace('[money]', purchaseWinTotal);

      guagualePurchasedUserText+=(app.translator.trans('wusong8899-guaguale.forum.guaguale-purchased-user',{
        purchaseUserName:purchaseUserName,
        purchaseWinTotal:purchaseWinTotalText,
        purchaseCount:purchaseCount
      })).join("")

      if(i<this.guagualePurchaseList.length-1){
        guagualePurchasedUserText+=" | ";
      }
    }

    if(guagualePurchasedUserText===""){
      guagualePurchasedUserText = app.translator.trans('wusong8899-guaguale.forum.guaguale-purchased-user-empty');
    }

    const guagualeTitleText = guagualeAvailable<=0?"["+app.translator.trans('wusong8899-guaguale.forum.guaguale-list-item-soldout-text')+"] "+guagualeData.title():guagualeData.title();
    const guagualeAvailableText = guagualeAvailable<=0?app.translator.trans('wusong8899-guaguale.forum.guaguale-list-item-soldout'):app.translator.trans('wusong8899-guaguale.forum.guaguale-list-item-available',{available:guagualeAvailable});
    const guagualeCostText = moneyName.replace('[money]', guagualeCost);
    let animationSpeed = guagualePurchaseList.length>2?(guagualePurchaseList.length+1)*5:15;

    const guagualeMarqueeStyle = "animation: marquee "+animationSpeed+"s linear infinite";

    return (
      <div class="GuaGuaLeContainer" onclick={() => this.showDetails(guagualeData,guagualeAvailable)} style={backgroundStyle}>
        <div class="GuaGuaLeMask" style={maskStyle}>
          <div class="GuaGuaLeTitle">
            {guagualeTitleText}
          </div>
          <div style="height:60px">
            <div class="GuaGuaLeDescription">
              {guagualeDesc}
            </div>
          </div>
          <div id="GuaGuaLeUserPurchaseList" class="GuaGuaLeUserPurchaseList">
            {!this.loading && (
              <p class="marquee">
               <span style={guagualeMarqueeStyle}>
                   {guagualePurchasedUserText}
               </span>
              </p>
            )}
          </div>
          <div class="GuaGuaLeDetails">
            {app.translator.trans('wusong8899-guaguale.forum.guaguale-list-item-cost',{cost:guagualeCostText})}&nbsp;|&nbsp;
            {guagualeAvailableText}
          </div>
        </div>
      </div>
    );
  }

  showDetails(guagualeData,guagualeAvailable) {
    //
    if (app.session.user) {
      app.modal.show(guagualeAvailable<=0?GuaGuaLeSoldOutModal:GuaGuaLePurchaseModal, {guagualeData});
    } else {
      app.modal.show(LogInModal);
    }
  }

  getBackgroundColor(str) {
    let hash = 0;
    for (let i = 0; i < str.length; i++) {
      hash = str.charCodeAt(i) + ((hash << 5) - hash);
    }
    let colour = '#';
    for (let i = 0; i < 3; i++) {
      let value = (hash >> (i * 8)) & 0xFF;
      colour += ('00' + value.toString(16)).substr(-2);
    }
    return colour;
  }

  parseResults(results) {
    [].push.apply(this.guagualePurchaseList, results);
    this.loading = false;
    m.redraw();

    return results;
  }

  loadResults(offset = 0) {
    return app.store
      .find("guagualePurchaseList", {
        guaID: this.attrs.guagualeData.id()
      })
      .catch(() => {})
      .then(this.parseResults.bind(this));
  }
}
