import Model from "flarum/Model";

export default class GuaGuaLePurchase extends Model {}
Object.assign(GuaGuaLePurchase.prototype, {
  id: Model.attribute("id"),
  title: Model.attribute("title"),
  gua_id: Model.attribute("gua_id"),
  user_id: Model.attribute("user_id"),
  pruchase_count: Model.attribute("pruchase_count"),
  pruchase_cost: Model.attribute("pruchase_cost"),
  pruchase_cost_total: Model.attribute("pruchase_cost_total"),
  pruchase_win_total: Model.attribute("pruchase_win_total"),
  pruchase_result: Model.attribute("pruchase_result"),
  opened: Model.attribute("opened"),
  assignedAt: Model.attribute("assigned_at"),
  openAt: Model.attribute("open_at"),
  guagualeData: Model.hasOne("guagualeData"),
  purchasedUser: Model.hasOne("purchasedUser"),
});
