import Component from "flarum/Component";
import app from "flarum/app";
import LoadingIndicator from "flarum/components/LoadingIndicator";
import Button from "flarum/components/Button";
import Link from "flarum/components/Link";

import GuaGuaLeHistoryListItem from "./GuaGuaLeHistoryListItem";

export default class GuaGuaLeHistoryList extends Component {
  oninit(vnode) {
    super.oninit(vnode);
    this.loading = true;
    this.moreResults = false;
    this.guagualeHistory = [];
    this.guagualeHistorySummary = null;
    this.user = this.attrs.params.user;
    this.loadResults();
  }

  view() {
    let loading;
    const moneyName = app.forum.attribute('antoinefr-money.moneyname') || '[money]';
    let purchaseCostTotalText,purchaseWinTotalText;

    if(this.loading){
      loading = LoadingIndicator.component({ size: "large" });
      purchaseCostTotalText = moneyName.replace('[money]', "-");
      purchaseWinTotalText = moneyName.replace('[money]', "-");
    }else{
      purchaseCostTotalText = moneyName.replace('[money]', this.guagualeHistorySummary.costTotal());
      purchaseWinTotalText = moneyName.replace('[money]', this.guagualeHistorySummary.winTotal());
    }

    return (
      <div>
        <div style="display:flex;padding-bottom:10px">
          <div style="font-size: 24px;font-weight: bold;display:inline-block;padding-right: 10px;">
            {app.translator.trans("wusong8899-guaguale.forum.guaguale-purchase-history")}
          </div>
          <Button className={'Button Button--primary'} onclick={() => m.route.set(app.route("guaguale"))}>
            {app.translator.trans("wusong8899-guaguale.forum.guaguale-purchase-now")}
          </Button>
        </div>
        <div style="color:var(--muted-color);padding-bottom:10px;">
          {app.translator.trans("wusong8899-guaguale.forum.guaguale-purchase-summary",{
            purchaseCostTotal:purchaseCostTotalText,
            purchaseWinTotal:purchaseWinTotalText
          })}
        </div>
        <ul style="margin: 0;padding: 0;list-style-type: none;position: relative;">
          {this.guagualeHistory.map((guagualeHistory) => {
            return (
              <li style="padding-bottom:5px" data-id={guagualeHistory.id()}>
                {GuaGuaLeHistoryListItem.component({ guagualeHistory })}
              </li>
            );
          })}
        </ul>
          
        {!this.loading && this.guagualeHistory.length===0 && (
          <div>
            <div style="font-size:1.4em;color: var(--muted-more-color);text-align: center;height: 300px;line-height: 100px;">{app.translator.trans("wusong8899-guaguale.forum.guaguale-history-list-empty")}</div>
          </div>
        )}

        {!loading && this.hasMoreResults() && (
          <div style="text-align:center;padding:20px">
            <Button className={'Button Button--primary'} disabled={this.loading} loading={this.loading} onclick={() => this.loadMore()}>
              {app.translator.trans('wusong8899-guaguale.forum.guaguale-history-list-load-more')}
            </Button>
          </div>
        )}

        {loading && <div className="GuaGuaLe-loadMore">{loading}</div>}
      </div>
    );
  }

  loadMore() {
    this.loading = true;
    this.loadResults(this.guagualeHistory.length).then(this.parseResults.bind(this));
  }

  parseResults(results) {
    this.moreResults = !!results.payload.links && !!results.payload.links.next;
    [].push.apply(this.guagualeHistory, results);

    return app.store
      .find("guagualePurchaseHistorySummary")
      .catch(() => {})
      .then((summaryResult) => {
        this.guagualeHistorySummary = summaryResult[0];
        this.loading = false;
        m.redraw();

        return results;
      });

  }

  hasMoreResults() {
    return this.moreResults;
  }

  loadResults(offset = 0) {
    return app.store
      .find("guagualePurchaseHistory", {
        filter: {
          user: this.user.id(),
        },
        page: {
          offset,
        },
      })
      .catch(() => {})
      .then(this.parseResults.bind(this));
  }
}
