import app from 'flarum/forum/app';
import Modal from 'flarum/components/Modal';
import Button from 'flarum/components/Button';

export default class GuaGuaLeScratchModal extends Modal {
  static isDismissible = false;

  oninit(vnode) {
    super.oninit(vnode);
    this.guagualeHistory = this.attrs.guagualeHistory;
    this.brushRadius = 30;
    this.opened = 0;
    this.guagualeWinPrice = 0;
  }

  onGuaualeResultContainerReady(vnode) {
    this.initTicket();
  }

  className() {
    return 'Modal--small';
  }

  title() {
    const guagualeData = this.guagualeHistory.guagualeData();
    return guagualeData.title();
  }

  content() {
    let guagualeWinPrice = 0;
    let guagualeWinPriceText = "";
    let guagualeResultList = [];
    const guagualeResult = JSON.parse(this.guagualeHistory.pruchase_result());
    const moneyName = app.forum.attribute('antoinefr-money.moneyname') || '[money]';

    for(let index in guagualeResult){
      let winAmount = parseInt(guagualeResult[index]);
      let winPrice = parseFloat(index);
      const winPriceText = moneyName.replace('[money]', winPrice);
      const unitName = app.translator.trans('wusong8899-guaguale.forum.guaguale-unit-name');

      guagualeResultList.push({text:winPriceText+" x"+guagualeResult[index]+unitName,winPrice:winPrice});

      if(winPrice!==0){
        guagualeWinPrice+=(winPrice*winAmount);
      }
    }

    this.guagualeWinPrice = this.precisionRound(guagualeWinPrice,2);
    guagualeWinPriceText = app.translator.trans('wusong8899-guaguale.forum.guaguale-scratch-result',{money:moneyName.replace('[money]', this.guagualeWinPrice)});

    return (
      <div className="Modal-body">
        <div className="Form">
          <div className="Form-group">
            <div id="guagualeResultContainer" oncreate={this.onGuaualeResultContainerReady.bind(this)} style="position: relative;-webkit-user-select: none;-ms-user-select: none;user-select: none;">
              <canvas style="z-index:999;position:absolute;border-radius:var(--border-radius)" id="ticketCanvas"></canvas>
              <div id="guagualeResultTextContainer" style="text-align:center;border:2px dotted;border-radius:var(--border-radius);visibility:hidden;">
                <div style="padding: 6px 10px;font-weight: bold;color: red;">{guagualeWinPriceText}</div>
                <div style="font-size: 10px;height: 65px;overflow-y: auto;padding: 0px 10px;display: flex;flex-wrap: wrap;justify-content: center;align-items: center;">
                  {guagualeResultList.map((guagualeResult) => {
                    const guagualeResultText = guagualeResult.text;
                    const guagualeResultWinPrice = guagualeResult.winPrice;
                    const borderColor = this.getBackgroundColor(guagualeResultWinPrice+"color");
                    const guagualeResultStyle = "height:27px;margin-top: 4px;padding:5px;border:2px "+borderColor+" solid;display: inline-block;margin-left: 4px;";
                    
                    return (
                      <div style={guagualeResultStyle}>
                        {guagualeResultText}
                      </div>
                    );
                  })}
                </div>
              </div>
            </div>
          </div>
          <div className="Form-group" style="text-align: center;">
            {Button.component(
              {
                className: 'Button Button--primary',
                loading: this.loading,
                onclick: () => {
                  // $('.Modal.modal-dialog.fade.Modal--small').fadeTo(0,0);
                  this.hide();
                }
              },
              app.translator.trans('wusong8899-guaguale.forum.guaguale-close')
            )}
          </div>
        </div>
      </div>
    );
  }

  initTicket(){
    //
    const canvasWidth = $("#guagualeResultContainer").parent().width();
    const canvasHeight = 100;

    $("#guagualeResultContainer").width(canvasWidth);
    $("#guagualeResultContainer").height(canvasHeight);
    $("#guagualeResultTextContainer").width(canvasWidth-4);
    $("#guagualeResultTextContainer").height(canvasHeight-4+5);
    document.getElementById('ticketCanvas').width = canvasWidth;
    document.getElementById('ticketCanvas').height = canvasHeight;

    const guagualeData = this.guagualeHistory.guagualeData();
    const guagualeImage = guagualeData.image();
    const guagualeTitle = guagualeData.title();
    const guagualeID = guagualeData.id();
    const guagualeOpened = this.opened = this.guagualeHistory.opened();

    this.ticketCanvas = document.getElementById('ticketCanvas');

    if(guagualeOpened===0){
      this.isDrawing = false; 
      this.lastPoint;
      this.ctx = this.ticketCanvas.getContext('2d',{willReadFrequently:true});

      let _this = this;

      if(guagualeImage){
        let image = new Image();
        image.src = guagualeImage;
        image.onload = function() {
          _this.ctx.drawImage(image, 0, 0, _this.ticketCanvas.width, _this.ticketCanvas.height);
          _this.ctx.beginPath();
          _this.ctx.rect(0, 0, canvasWidth, canvasHeight);
          _this.ctx.fillStyle = "rgba(0, 0, 0, 0.25)";
          _this.ctx.fill();
          _this.drawText(canvasWidth,canvasHeight);
        };
      }else{
        const guagualeColor = this.getBackgroundColor(guagualeTitle+"_"+guagualeID);
        this.ctx.beginPath();
        this.ctx.rect(0, 0, canvasWidth, canvasHeight);
        this.ctx.fillStyle = guagualeColor;
        this.ctx.fill();
        this.drawText(canvasWidth,canvasHeight);
      }

      this.brush = new Image();
      this.brush.src = 'data:image/png;base64,';
      
      this.ticketCanvas._this = this;
      this.ticketCanvas.addEventListener('mousedown', this.handleMouseDown, false);
      this.ticketCanvas.addEventListener('touchstart', this.handleMouseDown, false);
      this.ticketCanvas.addEventListener('mousemove', this.handleMouseMove, false);
      this.ticketCanvas.addEventListener('touchmove', this.handleMouseMove, false);
      this.ticketCanvas.addEventListener('mouseup', this.handleMouseUp, false);
      this.ticketCanvas.addEventListener('touchend', this.handleMouseUp, false);
    }else{
      this.ticketCanvas.style.display = "none";
      document.getElementById('guagualeResultTextContainer').style.visibility = 'visible';
    }
  }

  drawText(canvasWidth,canvasHeight){
    const scratchItText = app.translator.trans('wusong8899-guaguale.forum.guaguale-scratch-it');
    const pruchaseCountText = this.guagualeHistory.pruchase_count()+" "+app.translator.trans('wusong8899-guaguale.forum.guaguale-unit-name');

    this.ctx.font = "30px Arial";
    this.ctx.textAlign = 'center';
    this.ctx.textBaseline = 'middle';
    this.ctx.fillStyle = "white";
    this.ctx.fillText(scratchItText, parseInt(canvasWidth/2), parseInt(canvasHeight/2)-10);

    this.ctx.font = "16px Arial";
    this.ctx.fillText(pruchaseCountText, parseInt(canvasWidth/2), parseInt(canvasHeight/2)+30);
    document.getElementById('guagualeResultTextContainer').style.visibility = 'visible';
  }

  getBackgroundColor(str) {
    let hash = 0;
    for (let i = 0; i < str.length; i++) {
      hash = str.charCodeAt(i) + ((hash << 5) - hash);
    }
    let colour = '#';
    for (let i = 0; i < 3; i++) {
      let value = (hash >> (i * 8)) & 0xFF;
      colour += ('00' + value.toString(16)).substr(-2);
    }
    return colour;
  }
  
  distanceBetween(point1, point2) {
    return Math.sqrt(Math.pow(point2.x - point1.x, 2) + Math.pow(point2.y - point1.y, 2));
  }
  
  angleBetween(point1, point2) {
    return Math.atan2( point2.x - point1.x, point2.y - point1.y );
  }
  
  getFilledInPixels(stride) {
    if (!stride || stride < 1) { stride = 1; }
    
    let canvasWidth  = this.ticketCanvas.width;
    let canvasHeight = this.ticketCanvas.height;
    let pixels   = this.ctx.getImageData(0, 0, canvasWidth, canvasHeight),
        pdata    = pixels.data,
        l        = pdata.length,
        total    = (l / stride),
        count    = 0;
    
    for(let i = count = 0; i < l; i += stride) {
      if (parseInt(pdata[i]) === 0) {
        count++;
      }
    }
    
    return Math.round((count / total) * 100);
  }
  
  getMouse(e, ticketCanvas) {
    var offsetX = 0, offsetY = 0, mx, my;

    if (ticketCanvas.offsetParent !== undefined) {
      do {
        offsetX += ticketCanvas.offsetLeft;
        offsetY += ticketCanvas.offsetTop;
      } while ((ticketCanvas = ticketCanvas.offsetParent));
    }

    mx = (e.pageX || e.touches[0].clientX) - offsetX - window.pageXOffset;
    my = (e.pageY || e.touches[0].clientY) - offsetY - window.pageYOffset;

    return {x: mx, y: my};
  }
  
  handlePercentage(filledInPixels) {
    filledInPixels = filledInPixels || 0;
    // console.log(filledInPixels + '%');
    if (filledInPixels>=25) {
      $('#ticketCanvas').fadeTo(200,0);
      $('#ticketCanvas').css("pointer-events","none");
    }
  }
  
  handleMouseDown(e) {
    let _this = e.currentTarget._this;
    _this.isDrawing = true;
    _this.lastPoint = _this.getMouse(e, _this.ticketCanvas);

    if(_this.opened===0){
      _this.opened = 1;
      _this.openTicket();
    }
  }

  openTicket() {
    const guagualePurchaseID = this.guagualeHistory.id();
    this.guagualeHistory.save({guagualePurchaseID});

    const guagualeWinPrice = this.guagualeWinPrice;
    app.session.user.data.attributes.money+=guagualeWinPrice;

    if(app.store.data.guagualePurchaseHistorySummary){
      app.store.data.guagualePurchaseHistorySummary[""].data.attributes.winTotal+=guagualeWinPrice;
    }
  }

  precisionRound(number, precision) {
    let factor = Math.pow(10, precision);
    return Math.round(number * factor) / factor;
  }

  handleMouseMove(e) {
    let _this = e.currentTarget._this;

    if (!_this.isDrawing) { return; }
    
    e.preventDefault();

    let currentPoint = _this.getMouse(e, _this.ticketCanvas),
        dist = _this.distanceBetween(_this.lastPoint, currentPoint),
        angle = _this.angleBetween(_this.lastPoint, currentPoint),
        x, y;
    
    for (let i = 0; i < dist; i++) {
      x = _this.lastPoint.x + (Math.sin(angle) * i) - 25;
      y = _this.lastPoint.y + (Math.cos(angle) * i) - 25;

      _this.ctx.globalCompositeOperation = 'destination-out';
      _this.ctx.drawImage(_this.brush, x, y);
    }
    
    _this.lastPoint = currentPoint;
    _this.handlePercentage(_this.getFilledInPixels());
  }

  handleMouseUp(e) {
    let _this = e.currentTarget._this;
    _this.isDrawing = false;
    // $('#ticketCanvas').fadeTo(200,0);
  }
}
