import { extend } from 'flarum/extend';
import app from 'flarum/forum/app';
import IndexPage from 'flarum/components/IndexPage';
import LinkButton from 'flarum/components/LinkButton';

export default function addSidebarMenu() {
  extend(IndexPage.prototype, 'navItems', function (items) {
    let guagualeDisplayNameSetting = app.forum.attribute("guagualeDisplayName");
    let guagualeDisplayName = guagualeDisplayNameSetting===""?app.translator.trans('wusong8899-guaguale.forum.guaguale-display-name-default'):guagualeDisplayNameSetting;

    items.add(
      'GuaGuaLe',
      <LinkButton icon="fas fa-ticket-alt" href={app.route('guaguale')}>
        {guagualeDisplayName}
      </LinkButton>,
      15
    );

    return items;
  });
}
