import app from 'flarum/forum/app';
import Modal from 'flarum/components/Modal';
import Button from 'flarum/components/Button';

export default class GuaGuaLeSoldOutModal extends Modal {
  static isDismissible = false;

  oninit(vnode) {
    super.oninit(vnode);
    this.guagualeData = this.attrs.guagualeData;
  }

  className() {
    return 'Modal--small';
  }

  title() {
    return this.guagualeData.title();
  }

  content() {
    return (
      <div className="Modal-body">
        <div className="Form">
          <div className="Form-group">
            <div style="text-align: center;font-size: 18px;">{app.translator.trans('wusong8899-guaguale.forum.guaguale-purchase-soldout')}</div>
          </div>
          <div className="Form-group" style="text-align: center;">
            {Button.component(
              {
                className: 'Button Button--primary',
                loading: this.loading,
                onclick: () => {
                  this.hide();
                },
              },
              app.translator.trans('wusong8899-guaguale.forum.guaguale-purchase-ok')
            )}
          </div>
        </div>
      </div>
    );
  }
}
