import { extend } from "flarum/extend";
import UserPage from "flarum/components/UserPage";
import LinkButton from "flarum/components/LinkButton";
import GuaGuaLePurchaseHistoryPage from './components/GuaGuaLePurchaseHistoryPage';

export default function () {
  app.routes["user.guagualePurchaseHistory"] = {
    path: "/u/:username/guagualePurchaseHistory",
    component: GuaGuaLePurchaseHistoryPage,
  };

  extend(UserPage.prototype, "navItems", function (items,user) {
      const currentUserID = app.session.user.id();
      const targetUserID = this.user.id();

      if(currentUserID==targetUserID){
        items.add(
          "guagualePurchaseHistory",
          LinkButton.component({
              href: app.route("user.guagualePurchaseHistory", {
                username: this.user.username(),
              }),
              icon: "fas fa-ticket-alt",
            },
            [
              app.translator.trans(
                "wusong8899-guaguale.forum.guaguale-purchase-history"
              )
            ]
          ),
          10
        );
      }
  });
}
