import app from 'flarum/forum/app';
import Modal from 'flarum/components/Modal';
import Button from 'flarum/components/Button';
import GuaGuaLeScratchModal from './GuaGuaLeScratchModal';

export default class GuaGuaLePurchaseModal extends Modal {
  static isDismissible = false;

  oninit(vnode) {
    super.oninit(vnode);
    this.guagualeData = this.attrs.guagualeData;
    this.moneyName = app.forum.attribute('antoinefr-money.moneyname') || '[money]';
    this.moneyCost = 0;
  }

  className() {
    return 'Modal--small';
  }

  title() {
    return this.guagualeData.title();
  }

  content() {
    const moneyNameText = this.moneyName.replace('[money]', app.session.user.attribute("money"));
    const moneyCostText = this.moneyName.replace('[money]', 0);
    const availableAmount = this.guagualeData.amount()-this.guagualeData.purchased();

    return (
      <div className="Modal-body">
        <div className="Form">
          <div className="Form-group">
            <label>{app.translator.trans('wusong8899-guaguale.forum.guaguale-available-amount',{count:availableAmount})}</label>
            <input placeholder={app.translator.trans('wusong8899-guaguale.forum.guaguale-purchase-input-placeholder')} id="guagualePurchaseInput" required className="FormControl" type="number" step="1" min="1" max={availableAmount} onchange={() => this.updatePurchaseCost()} onkeyup={() => this.updatePurchaseCost()} />
            <div style="padding-top:10px">{app.translator.trans('wusong8899-guaguale.forum.guaguale-current-money-amount')}{moneyNameText}</div>
            <div>{app.translator.trans('wusong8899-guaguale.forum.guaguale-purchase-money')}<span id="guagualePruchaseMoney">{moneyCostText}</span></div>
          </div>
          <div className="Form-group" style="text-align: center;">
            {Button.component(
              {
                className: 'Button Button--primary',
                type: 'submit',
                loading: this.loading,
              },
              app.translator.trans('wusong8899-guaguale.forum.guaguale-purchase-confirm')
            )}&nbsp;
            {Button.component(
              {
                className: 'Button transferMoneyButton--gray',
                loading: this.loading,
                onclick: () => {
                  this.hide();
                }
              },
              app.translator.trans('wusong8899-guaguale.forum.guaguale-purchase-cancel')
            )}
          </div>
        </div>
      </div>
    );
  }

  updatePurchaseCost(){
    //
    let guagualePurchaseCount = $("#guagualePurchaseInput").val();
    const availableAmount = this.guagualeData.amount()-this.guagualeData.purchased();

    if(guagualePurchaseCount>availableAmount){
      guagualePurchaseCount = availableAmount;
      $("#guagualePurchaseInput").val(availableAmount);
    }

    const updatedValue = this.moneyCost = guagualePurchaseCount*this.guagualeData.cost();
    const updatedMoneyCostText = this.moneyName.replace('[money]', updatedValue);

    $("#guagualePruchaseMoney").text(updatedMoneyCostText);
  }

  onsubmit(e) {
    e.preventDefault();

    const guagualePurchaseCount = $("#guagualePurchaseInput").val();
    const guagualeID = this.guagualeData.id();

    if(guagualePurchaseCount>0){
      const guagualePurchaseData = {
        guagualeID:guagualeID,
        guagualePurchaseCount:guagualePurchaseCount,
      };

      this.loading = true;

      app.store
        .createRecord("guagualePurchase")
        .save(guagualePurchaseData)
        .then(
          (guagualeHistory) => {
            app.store.pushPayload(guagualeHistory);
            app.modal.show(GuaGuaLeScratchModal, {guagualeHistory});
            app.session.user.data.attributes.money-=this.moneyCost;
            this.loading = false;
          }
        )
        .catch((e) => {
          this.loading = false;
        });
    }
  }
}
