import app from 'flarum/forum/app';
import Notification from "flarum/components/Notification";

export default class GuaGuaLeNotification extends Notification {
  icon() {
    return "fas fa-ticket-alt";
  }

  href() {
    return app.route("user.guagualePurchaseHistory", {
      username: app.session.user.username(),
    });
  }

  content() {
    const notification = this.attrs.notification.subject();
    const guagualeTitle = notification.title();
    const guagualePurchaseCount = notification.pruchase_count();

    return app.translator.trans('wusong8899-guaguale.forum.notifications.guaguale-open-content', {
      guagualeTitle: guagualeTitle,
      guagualePurchaseCount:guagualePurchaseCount
    });
  }

  excerpt() {
    const notification = this.attrs.notification.subject();
    const guagualeWinTotal = notification.pruchase_win_total();
    const guagualeCostTotal = notification.pruchase_cost_total();
    const guagualePurchaseID = notification.id();
    const moneyName = app.forum.attribute('antoinefr-money.moneyname') || '[money]';
    const guagualeWinTotalText = moneyName.replace('[money]', guagualeWinTotal);
    const guagualeCostTotalText = moneyName.replace('[money]', guagualeCostTotal);

    return app.translator.trans('wusong8899-guaguale.forum.notifications.guaguale-win-total', {
      costTotal: guagualeCostTotalText,
      winTotal: guagualeWinTotalText,
      purchaseID: guagualePurchaseID
    });
  }
}
